<template>
  <div class="login">
    <Panel>
      <template #header>
        <router-link to="/">主页</router-link>
        <span>/</span>
        <span>登录</span>
      </template>
      <template #content>
        <el-form
          :model="ruleForm"
          hide-required-asterisk
          :rules="rules"
          ref="ruleForm"
          label-width="100px"
        >
          <el-form-item label="用户名" prop="username">
            <el-input
              style="width: 260px"
              type="text"
              v-model="ruleForm.username"
              autocomplete="off"
              placeholder="用户名任意输入"
            ></el-input>
          </el-form-item>
          <el-form-item label="密码" prop="password">
            <el-input
              placeholder="请输入用户 token"
              style="width: 260px"
              type="password"
              v-model="ruleForm.password"
              autocomplete="off"
            ></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="login()">登录</el-button>
          </el-form-item>
        </el-form>
      </template>
    </Panel>
  </div>
</template>

<script>
import { mapActions } from 'vuex';
import Panel from "../containers/Panel.vue";
export default {
  components: { Panel },
  data() {
    return {
      ruleForm: {
        username: "",
        // 输入的密码需要是用户的 token ，因为用户名密码登录功能没有，只有token登录功能
        password: "170de8cc-f596-4b9a-b397-3b282c8a8caf",
      },
      rules: {
        username: [
          { required: true, trigger: "blur", message: "用户名不能为空" },
        ],
        password: [
          {
            required: true,
            trigger: "blur",
            message: "密码不能为空",
          },
        ],
      },
    };
  },
  methods: {
      ...mapActions(['getInfo']),
     login() {
      this.$refs.ruleForm.validate((bool) => {
        if (bool) {
            this.getInfo(this.ruleForm.password).then(()=>{
                this.$router.push('/')
            }).catch(()=>{
                 this.$message.error('token输入有误，请检查并重新输入偶');
            })
            // console.log('出错了');
        }   
      });
    },
  },
};
</script>

